<template>
  <div class="home-user">
    <div class="user-view info">
      <a>
        <img :src="userAvatar" alt="用户头像" />
        <span class="user-view">{{ userName }}</span>
      </a>
    </div>
    <div class="news">
      <a><img quality="normal" data-once="true" :src="banner" style="width: 356px; height: 141px; border-radius: 6px" /></a>
    </div>
    <div class="notice">
      <div class="notice-title"><b>公告</b> 新鲜的事都在这里~</div>
      <div class="notice-item" v-for="(item, index) in noticeList" :key="index">
        <router-link :to="{path:`/notice/${item._id}`}">
          <span class="iconfont icon-daochu1024-07"></span>
          <span class="notice-item-right">{{ item.title }}</span>
        </router-link>
      </div>
    </div>
    <div class="myShopping">
      <div>
        <router-link to="/personalCenter/favorList">
          <i class="iconfont icon-shoucang user-icon"></i>
          <div>收藏的宝贝</div>
        </router-link>
      </div>
      <div>
        <router-link to="/personalCenter/orderList">
          <i class="iconfont icon-wodedingdan user-icon"></i>
          <div>我的订单</div>
        </router-link>
      </div>
      <div>
        <router-link to="/personalCenter/address">
          <i class="iconfont icon-huoyunzhandizhiguanli user-icon"></i>
          <div>地址管理</div>
        </router-link>
      </div>
      <div>
        <router-link to="/myTracks">
          <i class="iconfont icon-zuji user-icon"></i>
          <div>我的足迹</div>
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      banner: "",
      noticeList: [],
      defaultAvatar:
        "data:image/png;base64,",
    };
  },
  computed: {
    ...mapState(["login", "userInfo", "carList", "showCar"]),
    userAvatar() {
      return this.userInfo ? this.userInfo.avatar : this.defaultAvatar;
    },
    userName() {
      return this.userInfo
        ? `Hi! ${this.userInfo.username}`
        : "Hi~ 欢迎逛shopping商城!";
    },
  },
  created() {
    this.getNoticeList();
  },
  methods: {
    async getNoticeList() {
      const res = await this.$http.get("notice");
      const res2 = await this.$http.get("/ads");
      this.noticeList = res?.data[0]?.noticeList;
      this.banner = res2?.data[2]?.items[0]?.src;
    },
  },
};
</script>
<style lang="scss" scoped>
.home-user {
  width: 32.3%;
  background: #ededed;
  border-radius: 10px;
}

.user-view {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  align-content: flex-start;
  border: 0 solid #000;
  margin: 0;
  padding: 0;
  min-width: 0;
}

.info {
  margin: 20px auto 0;

  img {
    width: 80px;
    height: 80px;
    border-radius: 80px;
    margin: 0px auto;
    display: block;
  }

  span {
    color: #333;
    font-size: 12px;
    line-height: 16px;
    text-align: center;
    margin-top: 10px;
  }
}

.news {
  margin-top: 17px;
  margin-left: 14px;
  padding-bottom: 0px;

  a {
    width: 100%;
  }
}

.notice {
  margin: 27px 0 0 14px;

  .notice-title {
    color: #666;
    font-size: 14px;
    line-height: 14px;
    margin-bottom: 12px;
  }

  .notice-item {
    margin-bottom: 8px;
    width: 295px;
    overflow: hidden;
    white-space: nowrap;
    text-align: ellipsis;

    a {
      font-size: 12px;
      color: #666666;

      .notice-item-right {
        color: #666;
        font-size: 12px;
        line-height: 12px;
      }

      &:hover .notice-item-right {
        color: rgb(104, 147, 223);
      }
    }
  }
}

.icon-daochu1024-07 {
  color: rgb(255, 123, 21);
}

.myShopping {
  box-sizing: border-box;
  margin: 20px 23px 0 32px;
  width: 100%;
  overflow: hidden;
  color: #333;
}

.myShopping > div {
  float: left;
  margin-right: 34px;
  display: table-cell;
  text-align: center;
  vertical-align: middle;

  a {
    text-decoration: none;
    color: #333;

    .user-icon {
      color: rgb(51, 51, 51);
      font-size: 22px;
      -webkit-font-smoothing: antialiased;
    }

    div {
      font-size: 12px;
    }

    &:hover {
      color: rgb(104, 147, 223);
    }

    &:hover .user-icon {
      color: rgb(104, 147, 223);
    }
  }
}
</style>
